import React from "react";
import MultiCartItemAttributeComponent from "./MultiCartItemAttributeComponent";
import MultiCartItemIconCellComponent from "./MultiCartItemIconCellComponent";
import OraStockLevelStatusComponent from "eshop/components/OraStockLevelStatusComponent"
import DataLayerUtils from "eshop/utils/DataLayerUtils";
import {createSimCardDescription} from "./../utils/CartUtils";
import VASPackagesPresentationComponent from "./VASPackagesPresentationComponent";

const preventDefaultWrapper = (handler) => (event) => {
    event.preventDefault();
    handler(event);
}

export default class MultiCartItemHeaderMobileComponent extends MultiCartItemAttributeComponent {
    constructor (props) {
        super(props);
    }


    getMsisdnDescriptionForProcess() {
        return DataLayerUtils.processTypeToMsisdnDescriptionMap[this.props.processType];
    }

    getMsisdn() {
        return this.props.msisdnVerificationData ? this.props.msisdnVerificationData.msisdn : this.props.msisdn;
    }

    showChangeSimButton() {
        return this.props.onMsisdnChangeClicked && this.props.changeAllowed;
    }

    showLowerInstallments(){
        return this.props.onChangeClicked && this.props.lowerInstallmentsClicked && !!this.props.monthlyPricePresentation && !!this.props.monthlyPricePresentation.main && (this.props.monthlyPricePresentation.main.price != 0 || this.props.monthlyPricePresentation.main.priceWithoutVouchers != 0);
    }

    render() {
        return (
            <div>
                <div className="u-padding-left-l u-padding-right-l u-padding-l">
                  <table className="u-table-fixed">
                    <tbody>
                      <tr>
                        <MultiCartItemIconCellComponent entryType={this.props.type} image={this.props.imageUrl} icon={this.props.icon} terminalName={this.props.title}/>
                        <td>
                          <p className="h2">{ this.props.title }</p>
                          {this.props.loyaltyLengthDescription && <p className="u-small-padding-m"> {this.props.loyaltyLengthDescription} </p>}
                          {this.props.instalmentLengthDescription && <p> {this.props.instalmentLengthDescription} </p>}
                          { this.props.processType && this.props.editable
                          && [this.props.simCard && createSimCardDescription(this.props.simCard)
                                , this.getMsisdnDescriptionForProcess() + this.transformMsisdn(this.getMsisdn() ? this.getMsisdn() : '') + " "
                                , this.showChangeSimButton.apply(this)
                                  && <a href="#" key='msisdnChangeClicked_key'
                                        onClick={ preventDefaultWrapper(this.props.onMsisdnChangeClicked)}
                                        aria-label="zmień numer telefonu lub rodzaj karty sim">Zmień
                                     </a>
                            ]
                          }
                        <VASPackagesPresentationComponent className={this.props.editable ? "u-small-padding-top-m" : ""}
                                                          editable={this.props.editable}
                                                          onVasPackagesChangeClicked={this.props.changePackages}
                                                          vases={this.props.vases} bundleCode={this.props.bundleCode}/>
                          <OraStockLevelStatusComponent stockLevelStatus={this.props.stockLevelStatus} shouldStockLevelBeVisible={this.props.shouldStockLevelBeVisible}/>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <div>
                    {this.showLowerInstallments() &&
                    <table className="u-table-fixed">
                        <tbody>
                        <tr>

                            <td className={"u-padding-top-m u-padding-m u-padding-left-l u-padding-right-l u-text-center u-small-border-top" + (this.props.lowerInstallmentsClicked ? " u-border-r" : "")}>
                                <a className="u-text-decoration-none" onClick={this.props.lowerInstallmentsClicked}>{!!this.props.descriptions && this.props.descriptions.lowerInstallments || "Obniż raty"}</a>
                            </td>
                        </tr>
                        </tbody>
                    </table>}
                  <table className="u-table-fixed">
                    <tbody>
                      <tr>
                         {this.props.onChangeClicked && <td className="u-padding-top-m u-padding-m u-padding-left-l u-padding-right-l u-text-center u-small-border-top u-no-border-l">
                            <a className="u-text-decoration-none" onClick={this.props.onChangeClicked}>Zmień</a>
                        </td>}
                        {this.props.onDetailsClicked
                            && <td className={"u-padding-top-m u-padding-m u-padding-left-l u-padding-right-l u-text-center u-small-border-top" + (this.props.onChangeClicked ? " u-border-r" : "")}>
                            <a className="u-text-decoration-none" onClick={this.props.onDetailsClicked}>Szczegóły</a>
                        </td>}
                      </tr>
                    </tbody>
                  </table>
                </div>
            </div>
        )
    }
}